import { useSelector } from 'react-redux';

function Profiletitle(){
	let data=useSelector(state => state.about.profiletitle);

	return(
		<li>
            <ul>
                {data.map((d, i) =><li key={i+1}>{d}</li>)}
            </ul>
        </li>
	);
}

export default Profiletitle;