import { useEffect } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination } from "swiper/modules";
import 'swiper/css';
import "swiper/css/pagination";
import 'swiper/css/effect-fade';
import { useSelector } from 'react-redux';
import ProjectList from './ProjectList';


function Home(){
	let data=useSelector(state => state.projecttxt);

	let backgroundAniamtion=swiper => {
		let home=document.getElementById("home");
		let probg=home.firstElementChild.children;
		let mainN=swiper.realIndex;

		for(let i=0; i<probg.length; i++){
			if(i === mainN){
				probg[i].classList.add("active");
			}
			else{
				probg[i].classList.remove("active");
			}
		}
	};

	return(
		<section id="home" className="content">
			<div id="main-bg">
				<div className="project-bg"><img src="./assets/images/project1.png" alt="project1"/></div>
				<div className="project-bg"><img src="./assets/images/project2.png" alt="project2"/></div>
				<div className="project-bg"><img src="./assets/images/project3.png" alt="project3"/></div>
				<div className="project-bg"><img src="./assets/images/casestudy1.png" alt="case study1"/></div>
				<div className="project-bg"><img src="./assets/images/casestudy2.png" alt="case study2"/></div>
				<div className="project-bg"><img src="./assets/images/loding.png" alt="loding2"/></div>
			</div>
			<div id="main-ctr">
			<Swiper
				loop={true}
				pagination={{clickable: false}}
				breakpoints={{
					480:{
						slidesPerView: 1,
						spaceBetween: 10,
					},
					768:{
						slidesPerView: 2,
						spaceBetween: 30,
					},
					1020:{
						slidesPerView: 3,
						spaceBetween: 30,
					}
				}}
				onSwiper={swiper => {
						backgroundAniamtion(swiper);
					}
				}
				onSlideChange={swiper => {
					backgroundAniamtion(swiper);
				}}
				className={"ctr-swiper ctr-list"}
			>
				
					{
						data.map((d, i) => <SwiperSlide key={i+1}><ProjectList  props={d} /></SwiperSlide>)
					}
				
				<SwiperSlide>
					<a href=""> 
						<p className="label Loding-con"> .</p>
						<div className="square-line"></div>
					</a>
				</SwiperSlide>
			</Swiper>
			</div>
			<div id="profile-list">
				<ul>
					<li><a href="" target="portfolio"><span>Download<br/>Resume</span></a></li>
					<li><a href="https://codepen.io/hyesun1018" target="portfolio"><i className="fa-brands fa-codepen"></i></a></li>
					<li><a href="https://github.com/hyesun1018/portfolio.git" target="portfolio"><i className="fa-brands fa-github"></i></a></li>
				</ul>
			</div>
		</section>
	);
}

export default Home;