import { useSelector } from "react-redux";


function Gnb(){
	let data=useSelector(state => state.menu);

	return(
		<nav id="gnb">
			<div className="gnb-inner">
				<ul>
					{data.map((d, i) => <li key={(i+1)}><a href=""><span>{d}</span></a></li>)}
				</ul>
			</div>
		</nav>
	);
}

export default Gnb;