import { useEffect } from "react";
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Navigation } from 'swiper/modules';
import 'swiper/css';
import "swiper/css/pagination";
import 'swiper/css/navigation';

function Experience(){
	useEffect(()=>{	
		
	});

	const keytextString=[
		{
			title:"W Concept",
			client:"Client. W Concept",
			maintxt: "W Concept        .",
			detail: "",
			link: "https://jang-hye-sun.github.io/portfolio/casestudy1/index.html"
		},
		{
			title:"Ecc plus",
			client:"Client. YBM ECC",
			maintxt : "Ecc plus          .",
			detail: "",
			link: "https://jang-hye-sun.github.io/portfolio/casestudy2/index.html"
		},
		{
			title:"TOCOBO <br>  ",
			client:"Client. TOCOBO",
			maintxt : "          .",
			detail: "https://jang-hye-sun.github.io/portfolio/project1/project1_detail/index.html",
			link: "https://jang-hye-sun.github.io/portfolio/project1/index.html"
		},
		{
			title:"OLGODA <br>  ",
			client:"Client. OLGODA",
			maintxt : "(Olgoda)        .",
			detail: "https://jang-hye-sun.github.io/portfolio/project2/project2_detail/index.html",
			link: "https://jang-hye-sun.github.io/portfolio/project2/index.html"
		},
		{
			title:"GIOINFRA <br>  ",
			client:"Client. GIOINFRA",
			maintxt : "        .       GNB      UI ,       .",
			detail: "https://jang-hye-sun.github.io/portfolio/project3/project3_detail/index.html",
			link: "https://jang-hye-sun.github.io/portfolio/project3/index.html"
		}
	];
	
	let Ontext=swiper =>{
		let current, total;

		let exSlider=document.querySelector("#experience .exp-con");
		let [expswiper, expLi]=exSlider.children;

		let expswiLi=expswiper.firstElementChild.children;
		
		let exTitle=expLi.querySelector(".slide-title");
		let exClient=expLi.querySelector(".slide-client");
		let exmaintxt=expLi.querySelector(".slide-main-text");
		let exDes=expLi.querySelector(".slide-description");
		
		let [detailPG, detailLink]=exDes.children;

		current=swiper.realIndex;
		total=swiper.slides.length;

		expswiper.classList.add("active");
		expLi.classList.add("active");

		exTitle.innerHTML = keytextString[current].title;
		exClient.innerHTML = keytextString[current].client;
		exmaintxt.innerHTML = keytextString[current].maintxt;
		detailPG.setAttribute("href", keytextString[current].detail);
		detailLink.setAttribute("href", keytextString[current].link);
	}

	let OntextChange=swiper =>{
		let current, total;

		let exSlider=document.querySelector("#experience .exp-con");
		let [expswiper, expLi]=exSlider.children;

		let expswiLi=expswiper.firstElementChild.children;

		let exTitle=expLi.querySelector(".slide-title");
		let exClient=expLi.querySelector(".slide-client");
		let exmaintxt=expLi.querySelector(".slide-main-text");
		let exDes=expLi.querySelector(".slide-description");
		let [detailPG, detailLink]=exDes.children;

		current=swiper.realIndex;

		expLi.classList.remove("active");

		for(let i=0; i<expswiLi.length; i++){
			if(current >= 2){
				detailPG.classList.add("active");
			}
			else{
				detailPG.classList.remove("active");
			}
		}

		setTimeout(()=>{
			expLi.classList.add("active");

			exTitle.innerHTML = keytextString[current].title;
			exClient.innerHTML = keytextString[current].client;
			exmaintxt.innerHTML = keytextString[current].maintxt;
			exClient.setAttribute("href", keytextString[current].href);
			detailPG.setAttribute("href", keytextString[current].detail);
			detailLink.setAttribute("href", keytextString[current].link);
		}, 300);
	}
	
	return(
		<section id="experience" className="content">
			<div className="inner">
				<div className="title"><strong>EXPERIENCE</strong></div>
			</div>
			<div className="exp-con">
				<Swiper
					pagination={{
						el: '.swiper-pagination',
						type: 'progressbar',
					}}
					navigation= {{
						nextEl: '.swiper-button-next',
						prevEl: '.swiper-button-prev',
					}}
					loop={true}
					modules={[Pagination, Navigation]}
					onSwiper={swiper => {
						Ontext(swiper);
					}}
					onSlideChange={swiper => {
						OntextChange(swiper);
					}}
					className="exp-swiper exp-img"
				>
					<div className="swiper-wrapper">
						<SwiperSlide><img src="./assets/images/exp-casestudy1.png" alt="case1"/></SwiperSlide>
						<SwiperSlide><img src="./assets/images/exp-casestudy2.png" alt="case2"/></SwiperSlide>
						<SwiperSlide><img src="./assets/images/exp-project1.png" alt="project1"/></SwiperSlide>
						<SwiperSlide><img src="./assets/images/exp-project2.png" alt="project2"/></SwiperSlide>
						<SwiperSlide><img src="./assets/images/exp-project3.png" alt="project3"/></SwiperSlide>
					</div>
					<div className="swiper-pagination"></div>
				</Swiper>
				<div className="exp-list">
					<div className="slide-con">
						<p className="slide-title slide-text-up"></p>
					</div>
					<div className="slide-con">
						<p className="slide-client slide-text-up"></p>
					</div>
					<div className="slide-con">
						<h3 className="slide-main-text slide-text-up"></h3>
					</div>
					<div className="slide-description slide-con">
						<a href="" className="project-li slide-text-up" target="_blank">
						detail page
						</a>
						<a href="" className="slide-text-up" target="portfolio">
						link
						</a>
					</div>
					<div className="btn-li">
						<div className="swiper-button-next"></div>
						<div className="swiper-button-prev"></div>
					</div>
				</div>
			</div>
		</section>
	);
}

export default Experience;