function SkillList(props){
	let data=props.props;

	return(
		<li>
            <div className="sub-title"><span>{data.skillTitle}</span></div>
			<div className="sub-exp">
                {data.skilltxt.map((d, i) => <p key={i+1}>{d}</p>)}
            </div>
		</li>
	);
}

export default SkillList;